// components/NewsCard.tsx
type Props = {
  title: string;
  description: string;
  url: string;
  image: string;
  publishedAt: string;
};

export default function NewsCard({ title, description, url, image, publishedAt }: Props) {
  return (
    <div className="bg-white rounded-2xl shadow-md p-4 hover:shadow-xl transition">
      <img src={image} alt={title} className="w-full h-48 object-cover rounded-xl" />
      <h2 className="text-lg font-semibold mt-2">{title}</h2>
      <p className="text-gray-600 text-sm">{description}</p>
      <div className="flex justify-between items-center mt-3">
        <span className="text-xs text-gray-400">{new Date(publishedAt).toLocaleDateString()}</span>
        <a href={url} target="_blank" rel="noopener noreferrer" className="text-blue-500 underline text-sm">
          Read more
        </a>
      </div>
    </div>
  );
}
